<template>
  <div id="sscc_config">
    <el-form ref="formData" :model="formData" label-width="80px">
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="小站号" prop="userMark">
            <el-input v-model="formData.userMark"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="应用号"  prop="appMark">
            <el-input v-model="formData.appMark"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="主IP" prop="mainIp">
            <el-input v-model="formData.mainIp"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="主端口" prop="mainPort">
            <el-input v-model="formData.mainPort"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="备用IP" prop="spareIp">
            <el-input v-model="formData.spareIp"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="备用端口" prop="sparePort">
            <el-input v-model="formData.sparePort"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="密码" prop="dbPassword">
            <el-input v-model="formData.dbPassword"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="类型" prop="type">
            <el-select v-model="formData.type" placeholder="请选择">
              <el-option
                  v-for="item in type"
                  :key="item"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="addOrUpdate">提 交</el-button>
      </span>
  </div>
</template>

<script>
export default {
  props: {
    dialogVisible: {
      type: Boolean
    },
    selectedData: {
      type: Object
    }
  },
  emits: ['update:dialogVisible'],
  data() {
    //校验中文
    let validChinese = (rule, value, callback) => {
      if (value) {
        if (/[\u4E00-\u9FA5]/g.test(value)) {
          callback(new Error("不能输入中文"));
        } else {
          // 验证通过
          callback();
        }
        callback();
      }
    };

    return {
      //填写的表单数据
      formData: {},
      type: ["服务接收端","发送客户端"],
      rules: {
        userMark: [
          {required: true, message: '请输入小站号', trigger: 'blur'},
          {max: 30, message: '小站号最大输入30个字符',trigger: 'blur'}
        ],
        appMark: [
          {required: true, message: '请输入应用号', trigger: 'blur'},
          {max: 30, message: '应用号最大输入30个字符',trigger: 'blur'}
        ],
        mainIp:[
          {required: true, message: '请输入主IP', trigger: 'blur'},
          {max: 30, message: '主IP最大输入30个字符',trigger: 'blur'}
        ],
        mainPort: [
          {required: true, message: '请输入主端口', trigger: 'blur'},
          {max: 30, message: '主端口最大输入30个字符',trigger: 'blur'}
        ],
        spareIp: [
          {required: true, message: '请输入备用IP', trigger: 'blur'},
          {max: 30, message: '备用IP最大输入30个字符',trigger: 'blur'}
        ],
        sparePort: [
          {required: true, message: '请输入备用端口', trigger: 'blur'},
          {max: 30, message: '备用端口最大输入30个字符',trigger: 'blur'}
        ],
        dbPassword: [
          {required: true, message: '请输入备用端口', trigger: 'blur'},
          {max: 30, message: '备用端口最大输入30个字符',trigger: 'blur'},
          {validator: validChinese,trigger: "blur"}
        ]
      }
    }
  },
  methods: {
    addOrUpdate() {
    },
    close() {
      //关闭窗口
      this.formData = JSON.parse(JSON.stringify(this.selectedData))
      this.$emit('update:dialogVisible', false)
    }
  },
  created() {
    this.formData = this.selectedData;
  },
  watch: {
    selectedData: {
      immediate: true,
      handler(val) {
        this.formData = JSON.parse(JSON.stringify(val));
      }
    }
  }
}
</script>
