import Vue from 'vue'
import Router from 'vue-router'
import CustChanel from '../view/ChannelMaintain/list.vue';
import HomePage from '../view/welcom.vue';
import SsccConf from '../view/SSCC/list.vue';

export const routes =
    [
        {
            path: '/',
            name: 'home',
            component: HomePage,
            meta: { transition: 'slide-left' },
        },
        {
            path: '/CustChanel',
            name: 'CustChanel',
            component: CustChanel,
            meta: { transition: 'slide-right' },
        },
        {
            path: '/sscc',
            name: 'SsccConf',
            component: SsccConf,
            meta: { transition: 'slide-right' },
        }
    ];

Vue.use(Router);


export default new Router({
    mode: 'history',
    routes: routes,
    base: '/',
    scrollBehavior: () => ({ y: 0 })
})
