import axios from "axios";
import router from "../router";

let service = axios.create({
	headers: {'Content-Type':'application/json;charset=UTF-8'},
	timeout: 3*1000
});
service.defaults.withCredentials = true;
export default {
	service,
	post(data,timeout=3000){
		return service({
			url: 'home/trade',
			method: 'post',
			timeout: timeout,
			data: data
		});
	}
};
