<template>
  <div id="channelInfo">
    <el-form ref="formData" :model="formData" :rules="rules" label-width="80px">
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="通道名称" prop="name">
            <el-input v-model="formData.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="机构号" prop="deptCode1">
            <el-input v-model="formData.deptCode1"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="小站号" prop="userId">
            <el-input v-model="formData.userId"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="应用号" prop="appId">
            <el-input v-model="formData.appId"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-form-item label="协议" prop="protocol">
            <el-select v-model="formData.protocol" placeholder="请选择" disabled="true">
              <el-option
                  v-for="item in protocol"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="10">
          <el-form-item label="是否可用" prop="enable">
            <el-select v-model="formData.enable" placeholder="请选择">
              <el-option
                  v-for="item in enable"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="公司代码" prop="deptCode">
            <el-input v-model="formData.deptCode"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="addOrUpdate">提 交</el-button>
      </span>
  </div>
</template>

<script>
export default {
  props: {
    dialogVisible: {
      type: Boolean
    }
  },
  emits: ['update:dialogVisible'],
  data() {
    return {
      //填写的表单数据
      formData: {
        protocol:"A22",
        enable: true
      },
      protocol: [
        {value: 'A22', label: 'A22'},
        {value: '0', label: '宁波行'}
      ],
      enable: [
        {value: true, label: '是'},
        {value: false, label: '否'}
      ],
      rules: {
        name:[
            {required: true, message: '请输入通道名称', trigger: 'blur'},
            {max: 42, message: '通道名称最大输入42个汉字',trigger: 'blur'}
        ],
        deptCode1:[
          {required: true, message: '请输入机构号', trigger: 'blur'},
          {max: 20, message: '机构号最大输入20个字符',trigger: 'blur'}
        ],
        userId:[
          {required: true, message: '请输入小站号', trigger: 'blur'},
          {max: 30, message: '小站号最大输入30个字符',trigger: 'blur'}
        ],
        appId:[
          {required: true, message: '请输入应用号', trigger: 'blur'},
          {max: 30, message: '应用号最大输入30个字符',trigger: 'blur'}
        ],
        enable:[
          {required: true, message: '请选择是否可用', trigger: 'blur'},
        ],
        deptCode:[
          {required: true, message: '请输入公司代码', trigger: 'blur'},
        ]
      }
    }
  },
  methods: {
    addOrUpdate() {
    },
    close() {
      //关闭窗口
      this.formData = {
        protocol:"A22",
            enable: true
      },
      this.$emit('update:dialogVisible', false)
    }
  }
}

</script>
