<template>
  <div>
    <el-form :inline="true" :model="queryFormData" class="demo-form-inline">

      <el-row :gutter="20">
        <el-form-item class="queryParam" label="通道名称">
          <el-input v-model="queryFormData.name"></el-input>
          {{queryFormData.name}}
        </el-form-item>
        <el-form-item class="queryParam" label="机构号">
          <el-input v-model="queryFormData.deptCode"></el-input>
        </el-form-item>
        <el-button class="queryParam" type="primary" @click="selectByPage">查询</el-button>
        <el-button class="queryParam" type="primary" @click="resetQuery">重置</el-button>
      </el-row>
    </el-form>
    <div style="float: left;">
      <el-button type="primary" @click="showAdd">新增</el-button>
      <el-button type="primary" @click="showEdit">修改</el-button>
      <el-button type="primary" @click="deleteChannel">删除</el-button>
    </div>
    <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="id" v-if="false" width="1"></el-table-column>
      <el-table-column prop="name" label="通道名称" width="300" align="center"></el-table-column>
      <el-table-column prop="deptCode" label="机构号（组织机构代码证号）" width="300" align="center"></el-table-column>
      <el-table-column prop="userId" label="小站号" width="100" align="center"></el-table-column>
      <el-table-column prop="appId" label="应用号" width="100" align="center"></el-table-column>
      <el-table-column prop="deptCode" label="公司代码" width="100" align="center"></el-table-column>
      <el-table-column prop="crtTime" label="创建时间" width="140" align="center"></el-table-column>
    </el-table>

    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-sizes="[10,20,50,100]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total"
        style="float: left">
    </el-pagination>


    <el-dialog
        title="通道维护"
        :visible.sync="dialogVisible"
        width="30%">
      <div v-if="dialogType == 'add'">
        <AddForm :dialogVisible.sync="dialogVisible"></AddForm>
      </div>
      <div v-else-if="dialogType == 'edit'">
        <EditForm :dialogVisible.sync="dialogVisible" :selectedData.sync="multipleSelection[0]"></EditForm>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import AddForm from "./add.vue"
import EditForm from "./edit.vue"

export default {
  components: {AddForm, EditForm},
  data() {
    return {
      queryFormData: {}, //查询表单
      dialogVisible: false,
      dialogType: "",
      tableData: [],
      multipleSelection: [],//表格勾选数据
      pagination: {
        currentPage: 1, //当前页   4
        pageSize: 5,    //每页显示条数
        total: 0        //总条数
      }
    }
  },
  methods: {
    showAdd() {
      this.dialogType = "add";
      this.dialogVisible = true;
    },
    showEdit() {
      let dataSize = this.multipleSelection.length;
      if (dataSize <= 0) {
        this.$message.error('请选择一条数据');
        return;
      }

      if (dataSize > 1) {
        this.$message.error('只能选择一条数据，请重试');
        return;
      }
      this.dialogType = "edit";
      this.dialogVisible = true;
    },
    closeDialog() {
      this.dialogVisible = false;
    },
    deleteChannel() {
      let dataSize = this.multipleSelection.length;
      if (dataSize <= 0) {
        this.$message.error('请选择要删除的数据数据');
        return;
      }
      //todo 调用删除方法

      this.$confirm('此操作将永久删除数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios
            .post("", this.multipleSelection)
            .then(

            ).catch(

        )
      }).catch(() => {
        this.$message.info("已取消删除");
      });

    },
    //改变每页条数时执行的函数
    handleSizeChange(pageSize) {
      //修改分页查询的参数
      this.pagination.pageSize = pageSize;
      //重新执行查询
      this.selectByPage();
    },
    //改变页码时执行的函数
    handleCurrentChange(pageNum) {
      //修改分页查询的参数
      this.pagination.currentPage = pageNum;
      //重新执行查询
      this.selectByPage();
    },
    resetQuery() {
      this.queryFormData = {};
    },
    handleSelectionChange(row) {
      this.multipleSelection = row;
    },
    selectByPage() {
	  let queryForm = {
		  customerName : this.queryFormData.name,
		  orgCode: this.queryFormData.deptCode
	  };
      let reqData = this.$commonFun.requestHttpData(queryForm);
	  console.log(reqData);
 this.$axios
          .post("home/trade",reqData)
          .then(resp => {
              console.log(resp);
              this.tableData = resp.data
              }
          ).catch(

      )}
  },
  created() {
    this.selectByPage();
  }
}
</script>
<style>
.queryParam {
  float: left;
}

.el-dialog__title {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-weight: bold;
  font-size: 20px;
}
</style>
