import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router/index';
import axios from "axios";

import "./js/permission.js";
import commonFun from "./js/common.js";
import commonData from "./js/data.js";
import request from "./js/request.js";
import Message from "./js/message.js";


Vue.config.productionTip = false
Vue.use(ElementUI);
Vue.prototype.$axios = axios;

Vue.prototype.$message = Message.$message;
Vue.prototype.$EvenBus = new Vue();
Vue.prototype.$request = request;
Vue.prototype.$commonFun = commonFun;
Vue.prototype.$commonData = commonData;

Vue.directive("moMoreClick",{
	inserted(el){
		el.addEvenListener('click',() => {
			el.classList.add('is-disabled');
			el.disabled = true;
			
		})
	}
});

new Vue({
    router,
    render: h => h(App),
}).$mount('#app')
